li {
    list-style-type: none;
}

.card {
    border: 1px solid rgb(187, 27, 137);
    max-width: 100%;
    background: -webkit-gradient(linear, left top, right top, from(rgba(203,67,53,0)), to(rgba(203,67,53,0.5)));
    background: -webkit-linear-gradient(left, rgba(203,67,53,0), rgba(203,67,53,0.5));
    background: -o-linear-gradient(left, rgba(203,67,53,0), rgba(203,67,53,0.5));
    background: linear-gradient(to right, rgba(203,67,53,0), rgba(203,67,53,0.5));
}

hr {
    border: 1px solid rgb(187, 27, 137);
}
#saved {
    height: 100%;
    width: 100%
}
.list-overflow-container {
    max-height: 100%;
}

#labeltext {
    font-weight: 800;
    font-size: 4rem;
    color:  #840204;
   /* font-family: 'Sedgwick Ave Display', cursive;*/
    font-family: 'Courgette', cursive;
}

li.list-group-item {
    padding: 15px;
}

.recipe-label-setting {
   color: green;
   font-size: 125%;
   font-weight: bold;
   margin-bottom: 2%;
   font-family: 'Slabo 27px', serif;
}

.recipe-link:link, .recipe-link:hover, .recipe-link:active, .recipe-link:visited  {
    text-decoration: none;
    color: green;
}
.list-group-item{
    display: block;
}

.card-title{
    font-size: 40px;
    text-align: center;
    margin-top:10px;
    font-weight: bold; 
    font-family: 'Slabo 27px', serif; 
}
.list-overflow-container li a {
    color:darkblue;
    text-decoration: none;
}

h1{
    font-family: 'Slabo 27px', serif;
    font-style: inherit;
    font-weight: bold;
    font-size: 3.3rem;
}

label {
     font-family: 'Slabo 27px', serif;
     font-weight: bold;
     color:  #840204;
     font-size: 1.2rem;
}


